import axios from 'axios'
import qs from 'qs'
import $u from '../utils/utils'
import router from "../router"

let instance = axios.create({
  baseURL: "",
  timeout: 1000 * 60 * 5,
  headers: {
    // "Content-Type": "application/x-www-form-urlencoded",
    // "Content-Type": "multipart/form-data",
    "Content-Type": "application/json",
    // Authorization: "Bearer " + token,
    // timeStamp: Date.parse(new Date()) / 1000,
    app: 0
  },
  // transformRequest: [(data) => {
  //   return data
  // }],
  // transformResponse: [(data) => {
  //   return JSON.parse(data)
  // }],
  paramsSerializer: (params) => {
    return qs.stringify(params, {
      arrayFormat: 'brackets'
    })
  },
  params: {},
  data: {},
  // 
  // adapter: (config) => {
  // },
  responseType: "json",
  onUploadProgress: (progressEvent) => {
    if (progressEvent.total >= 1024 * 1024) {
      let persent = parseInt(((progressEvent.loaded / progressEvent.total) * 100)) || 0
      $u.showProgress(persent)
      if (persent == 100) setTimeout(() => {
        $u.hideProgress()
      }, 500)
    }
  },
  onDownloadProgress: (progressEvent) => {
    // console.log("", progressEvent)
  },
  validateStatus: (status) => {
    return status >= 200 && status < 300
  }
})

// request before -> set

instance.interceptors.request.use(async config => {
  // --------------------  --------------------
  config.params.timeStamp = Date.parse(new Date()) / 1000

  // -------------------- token --------------------
  let token = localStorage.getItem("token")

  // --------------------  --------------------
  // ["form", "file", "exParam","exData", "arrParam", "noToken", "customError", "getArr", "integral"]
  // params 

  for (let key in config.params) {
    let Str = String(config.params[key])
    if (Str == 'undefined' || Str == 'null' || Str == '') delete config.params[key]
  }

  for (let key in config.data) {
    let Str = String(config.data[key])
    if (Str == 'undefined' || Str == 'null' || Str == '') delete config.data[key]
  }

  // GET 
  // POSTPUT...  ()form()()
  if (config.exConfig) {
    if (config.exConfig.includes("form")) {
      config.headers["Content-Type"] = "application/x-www-form-urlencoded"
      config.data = qs.stringify(config.data)
    }
    if (config.exConfig.includes("file")) config.headers["Content-Type"] = "multipart/form-data"
    if (config.exConfig.includes("exParam")) config.params = config.exData
    if (config.exConfig.includes("exData")) config.data = config.exData
    if (config.exConfig.includes("getArr")) config.paramsSerializer = () => {
      return qs.stringify(config.params, {
        arrayFormat: 'repeat'
      })
    }

    // 
    // if (config.exConfig.includes("integral")) {
    //   let appStatus = JSON.parse(localStorage.getItem("appStatus"))
    //   let info = JSON.parse(localStorage.getItem(`${appStatus[1]}Info`))
    //   if (info) {
    //     config.params.OrgId = info.OrgId
    //     config.params.OrgName = info.OrgName
    //     config.params.OrgLongName = info.OrgLongName
    //     config.params.MemberId = info.Id
    //   }
    // }
  }
  config.headers["Authorization"] = 'Bearer ' + token
  return config

}, (error) => {
  Promise.reject(error)
})

// 
let statusMsg = {
  400: " ",
  401: " 192",
  403: " ",
  404: ", ",
  405: " ",
  408: " ",
  500: " ",
  501: " ",
  502: " ",
  503: " ",
  504: " ",
  505: "http "
}

// request after -> handle data
instance.interceptors.response.use((response) => {
  // 2  errorcode0 1  errcode
  // res
  if ("errcode" in response.data) {
    if (response.data.errcode == 0) {
      return response.data
    } else {
      // $u.msg("", "error")
      $u.msg(response.data.msg, "error")
      return null
    }
  } else {
    return response.data
  }
}, (error) => {
  if (error && error.response) {

    // 
    //   
    // token /oauth/connect/token  400 
    // if (error.response.config.url = "/oauth/connect/token") {
    //   $u.msg(error.response.data.error_description, "warning", 200)
    //   return null
    // }

    //  
    if (error.response.config.exConfig && error.response.config.exConfig.includes("customError")) {
      return error.response.data
    }

    // 

    $u.msg(statusMsg[error.response.status] ? statusMsg[error.response.status] : "", "error")
    if (error.response.status == 401) {
      // 
      $u.msg("240", "error")
      setTimeout(() => {
        router.push('/login')
      }, 500)
    }
    return null
  }
})

export default instance